import { plusready, plusOpen } from "common/plus";
export default {
  props: ["tjLoad", "data", "cycl"],
  data() {
    let self = this;
    return {
      // amapManager,
      zoom: 15,
      center: [0, 0],
      events: {
        init: o => {
          self.$emit("loadEnd");
          o.getCity(result => {
            // console.log(result);
          });
        }
      },
      polyline: {
        path: [],
        strokeColor: "#0EB273",
        strokeWeight: 4,
        events: {
          end: e => {
            let newPath = e.target
              .getPath()
              .map(point => [point.lng, point.lat]);
          }
        },
        editable: false
      },
      plugin: [
        {
          pName: "MapType",
          defaultType: 0,
          events: {
            init(instance) {}
          }
        },
        {
          pName: "Geolocation",
          events: {
            init(o) {
              // o 是高德地图定位插件实例
              o.getCurrentPosition((status, result) => {
                if (result && result.position) {
                  // self.lng = result.position.lng;
                  // self.lat = result.position.lat;
                  // if(self.center == [0,0]){
                  //   self.center = [self.lng, self.lat];
                  //    self.$nextTick();
                  //   return
                  // }
                  self.$nextTick();
                }
              });
            }
          }
        }
      ],
      window: {
        position: [121.59996, 31.197646]
      },
      visible:false,
      marker: {
        position: [120.11295, 30.34268],
        offset: [-35, -72],
        events: {
          click: () => {
            self.$emit("mapListen");
            // self.$nextTick(() => {
            //   self.visible = true;
            // });
          }
        },
        icon: require("assets/img/carsWhite.png")
      }
    };
  },
  watch: {
    data(val) {
      this.getStop(val);
    }
  },
  methods: {
    getStop(val) {
      this.visible = true;
      this.center = [val.latitude, val.longitude];
      this.marker.position = this.center;
      this.window = {
        position: [val.latitude - 0.0005, val.longitude + 0.0028]
      };
      if (val.chargingStatus == 1) {
        this.marker.icon = require("assets/img/carsGreen.png");
      } else if (val.alarmStatus == 0) {
        this.marker.icon = require("assets/img/cars.png");
      }
      if (val.stopStatus == 1) {
        this.polyline.path.push(this.center);
      }
    }
  }
};



